<template>
  <v-app id="admin_login">
    <v-toolbar id="toolbar" color="transparent" dark app flat>
      <v-spacer></v-spacer>
      <v-tooltip bottom>
        <v-btn slot="activator" icon large target="_blank" to="/">
          <v-icon large>store</v-icon>
        </v-btn>
        <span>返回购物大厅</span>
      </v-tooltip>
    </v-toolbar>
    <v-jumbotron src="/static/triangle2.jpg" dark height="200px" :gradient="gradient">
      <v-container fill-height>
        <v-layout align-center class="text-lg-left">
          <v-flex>
            <h3 class="display-2">{{title}}</h3>
            <span class="subheading">{{sub_title}}</span>
          </v-flex>
        </v-layout>
      </v-container>
    </v-jumbotron>
    <v-content id="content">
      <v-container>
        <v-layout align-center class="text-lg-left">
          <v-flex xs12 sm7>
            <div class=" headline  mb-2 " id="welcome">{{welcome}}</div>
            <em class="grey--text">注意阅读右方提示信息噢</em>
            <v-divider  class="my-3"></v-divider>
            <v-card>
              <v-card-text>
                <v-form :model="loginData">
                  <v-text-field v-model="loginData.name" color="blue-grey darken-2" prepend-icon="person" label="用户名" type="text"></v-text-field>
                  <v-text-field v-model="loginData.key" color="blue-grey darken-2" prepend-icon="lock" label="密码" id="password" type="password"></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn dark color="blue-grey darken-2" @click="login">登录</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
          <v-flex xs12 sm6 offset-sm1>
            <v-card class="elevation-0 transparent">
              <v-card-text id="encourage">{{encourage}}</v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
      <br/>
      <br/>
      <v-footer class="blue-grey darken-2" id="footer">
        <v-layout row wrap align-center>
          <v-flex xs12 text-xs-center>
            <div class="white--text ml-3">
              Made with
              <v-icon class="red--text">favorite</v-icon>
              by AlphaCat Team
            </div>
          </v-flex>
        </v-layout>
      </v-footer>
    </v-content>
  </v-app>
</template>

<script>
  import bus from '@/eventBus'
  export default {
    name: "PurchaserLogin",
    data() {
      return {
        gradient: 'to top right, rgba(0,0,128,.6), rgba(136,0,128, .6)',
        title: 'Lavender购物平台',
        sub_title: 'Lavender Shopping Mall',
        welcome: 'Hi, 亲爱的用户, 欢迎登录Lavender购物平台~',
        encourage: '您的每一份信任，都会为我们的用心增添力量。希望给您带来愉快的购物体验！',
        loginData : {
          name:'',
          key:'',
          type: 'purchaser',
        },
      }
    },
    methods: {
      login() {
        if (this.loginData.name === '') bus.$emit('show-info', "未输入登录名！");
        else if (this.loginData.key === '') bus.$emit('show-info', "未输入登录密码！");
        else {
          this.$http.post('/login', this.loginData)
            .then((response) => {
              let result = response.data.result;
              if (result === 'success') {
                sessionStorage.setItem('id', response.data.id);
                sessionStorage.setItem('name', response.data.name);
                sessionStorage.setItem('type', response.data.type);
                sessionStorage.setItem('logged', 'true');
                this.$router.push("/");
              } else {
                bus.$emit('show-info', result);
              }
            })
            .catch(function (error) {
              console.log(error);
            });
        }
      }
      }
  }
</script>

<style scoped>

  #welcome{
    color: #616161;
    text-shadow: 1px 1px 1px #455A64;
  }

  #encourage{
    color: #455A64;
    font-size: large;
  }
  #footer{
    opacity: 0.8;
  }
</style>



