<template>
  <v-app id="inspire">
    <v-toolbar color="orange" dark fixed app clipped-right id="toolbar">
      <v-toolbar-title>{{barTitle}}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-tooltip bottom>
        <v-btn slot="activator" icon large target="_blank" to="/">
          <v-icon large>store</v-icon>
        </v-btn>
        <span>返回购物大厅</span>
      </v-tooltip>
      <v-tooltip bottom>
        <v-btn slot="activator" icon large target="_blank" @click="logout">
          <v-icon large>exit_to_app</v-icon>
        </v-btn>
        <span>退出登录</span>
      </v-tooltip>
    </v-toolbar>
    <v-navigation-drawer :mini-variant.sync="mini" v-model="drawer" stateless hide-overlay mini-variant-width="85" app class="grey lighten-4">
      <v-toolbar flat >
        <v-list class="pa-0">
          <v-list-tile avatar>
            <v-list-tile-avatar size="52px" class="">
              <img src="/static/publisher.jpg">
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title>{{purchaserName}}</v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-action>
              <v-btn icon @click.native.stop="mini = !mini">
                <v-icon>chevron_left</v-icon>
              </v-btn>
            </v-list-tile-action>
          </v-list-tile>
        </v-list>
      </v-toolbar>
      <v-divider dark class="my-0"></v-divider>
      <v-list dense>
        <template v-for="(item, i) in items">
          <v-divider v-if="item.divider" :key="i" dark class="my-3"></v-divider>
          <v-list-tile v-else :key="i" :to="item.path" @click="barTitle=item.text">
            <v-list-tile-action>
              <v-icon class="orange--text text--darken-2">{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
               <v-list-tile-title class="grey--text">
                {{ item.text }}
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </template>
      </v-list>
    </v-navigation-drawer>
    <v-content>
      <router-view/>
    </v-content>
    <v-footer class="orange darken-2" id="footer" app>
      <v-layout row wrap align-center>
        <v-flex xs12 text-xs-center>
          <div class="white--text ml-3">
            Made with
            <v-icon class="red--text">favorite</v-icon>
            by Lavender
          </div>
        </v-flex>
      </v-layout>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  name: 'PurchaserPage',
  data: () => ({
    drawer: true,
    mini: false,
    barTitle: "购物车",
    purchaserName: sessionStorage.getItem('name'),
    noteLength: 0,
    items: [
      { icon: 'shopping_cart', text: '购物车',  path: '/purchaser/basket'},
      { icon: 'star_border', text: '收藏夹',  path: '/purchaser/collection' },
      { icon: 'poll', text: '购买记录',  path: '/purchaser/bought' },
      { divider: true },
      { icon: 'local_atm', text: '充值', path: '/purchaser/recharge' },
      { icon: 'face', text: '个人信息', path: '/purchaser/info'},
    ]
  }),
  methods:{
    logout(){
      sessionStorage.setItem('logged', 'false');
      this.$router.push("/login/purchaser");
    }
  }
}
</script>

<style scoped>

</style>



