import VueRouter from 'vue-router'
import PurchaserPage from './components/PurchaserPage'
import index from './components/index'
import GoodsInfo from './components/GoodsInfo'
import Register from './components/Register'
import PurchaserLogin from './components/PurchaserLogin'
import Basket from './components/Basket'
import Collection from './components/Collection'
import Bought from './components/Bought'
import PurchaseInfo from './components/PurchaserInfo'
import Recharge from './components/Recharge'
const router = new VueRouter({
  routes: [
    {
      path: '/',
      name: 'index',
      meta: {title: '首页 | Lavender购物平台'},
      component: index
    },
    {
      path: '/login/purchaser',
      name: 'PurchaserLogin',
      meta: {title: '登录 | Lavender购物平台'},
      component: PurchaserLogin
    },
    {
      path: '/register',
      name: 'Register',
      meta: {title: '用户注册 | Lavender购物平台'},
      component: Register
    },
    {
      path: '/purchaser',
      name: 'PurchaserPage',
      component: PurchaserPage,
      children: [
        {
          path: 'basket',
          component: Basket,
          meta: {title: '购物车 | Lavender购物平台'}
        },
        {
          path: 'collection',
          component: Collection,
          meta: {title: '收藏夹 | Lavender购物平台'}
        },
        {
          path: 'bought',
          component: Bought,
          meta: {title: '已购买物品 | Lavender购物平台'}
        },
        {
          path: 'info',
          component: PurchaseInfo,
          meta: {title: '个人信息 | Lavender购物平台'}
        },
        {
          path: 'recharge',
          component: Recharge,
          meta: {title: '充值中心 | Lavender购物平台'}
        }
      ]
    },
    {
      path: '/goods/:id',
      name: 'GoodsInfo',
      component: GoodsInfo,
      meta: {title: '商品信息 | Lavender购物平台'}
    }
  ]
})
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})
export default router



