<template>
<v-container>
  <div class="display-2 grey--text pt-4 pl-3">您的账户余额为：{{purchaserInfo.balance}}</div>
  <div class="display-1 grey--text pl-3 pt-3">请输入充值金额</div>
  <v-flex xs6 class="ml-4 mt-3">
    <div><v-text-field v-model="rechargeData.money" type="number" min="0" color="orange" label="充值金额"></v-text-field></div>
  </v-flex>
  <div class="pl-4"><v-btn color="orange" dark @click="recharge">确认充值</v-btn></div>
</v-container>
</template>

<script>
  import bus from '@/eventBus'
export default {
  name: 'Recharge',
  data() {
    return {
      rechargeData:{
        purId: sessionStorage.getItem('id'),
        money: 0
      },
      purchaserInfo: {},
    }
  },
  methods:{
    getPurchaserInfo(){
      this.$http.get('/purchaserInfo/'+sessionStorage.getItem('id')).then((response)=>{
          this.purchaserInfo=response.data;
        }
      );
    },
    recharge(){
      this.$http.post('/recharge',this.rechargeData).then((response)=>{
        bus.$emit('show-info', response.data.result);
        this.getPurchaserInfo();
      })
    },
  },
  created() {
    this.getPurchaserInfo()
  }
}
</script>

<style scoped>

</style>



