<template>
  <v-container v-if="goodsList.length>0">
    <v-layout class="align-center justify-center">
      <v-flex xs10 class="mt-2">
        <v-card flat>
          <v-container fluid style="min-height: 0;" grid-list-lg>
            <v-layout row wrap>
              <v-flex xs12 v-for="(goods ,i) in goodsList" :key="i">
                <v-card color="grey lighten-3" class="orange--text text--darken-3" hover>
                  <v-container fluid grid-list-lg>
                    <v-layout row>
                      <v-flex xs3 class="pl-0">
                        <v-card-media src="/static/publisher.jpg" height="135px" contain></v-card-media>
                      </v-flex>
                      <v-flex xs6>
                        <div>
                          <div class="headline">{{goods.name}}</div>
                          <div class="pt-2">
                            购买时间：{{goods.time}}<br/>
                            <font-awesome-icon icon="yen-sign" />{{goods.price}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;购买数量：{{goods.num}}<br/>
                          </div>
                          <v-divider></v-divider>
                          <div>合计：{{goods.num*goods.price}}</div>
                        </div>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </v-card>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
  <v-container v-else>
    <v-layout class="align-center justify-center">
      <div class="display-2 grey--text pt-4 pl-3"><v-icon large>mood_bad</v-icon>还未有购买记录哦，快去购买吧~</div>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: 'Bought',
  data() {
    return {
      goodsList:[],
    }
  },
  methods: {
    getGoodsList() {
      this.$http.get('/bought?id='+sessionStorage.getItem('id') )
        .then((response) => {
          this.goodsList = response.data
        })
    },
  },
  created() {
    this.getGoodsList()
  }
}
</script>

<style scoped>

</style>



