<template>
  <v-app>
    <v-toolbar v-if="isLoggedIn === false" color="orange" dark fixed app clipped-right id="toolbar1">
      <v-toolbar-title>商品详情</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items >
        <v-btn slot="activator" color="transparent" depressed target="_blank" to="/login/purchaser">请登录</v-btn>
        <v-btn slot="activator" color="transparent" depressed target="_blank" to="/register">注册</v-btn>
      </v-toolbar-items>
      <v-tooltip bottom>
        <v-btn slot="activator" icon large target="_blank" to="/">
          <v-icon large>store</v-icon>
        </v-btn>
        <span>返回购物大厅</span>
      </v-tooltip>
    </v-toolbar>
  <v-toolbar v-else color="orange" dark fixed app clipped-right id="toolbar2">
    <v-toolbar-title>商品详情</v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items>
      <v-btn slot="activator" color="transparent" depressed target="_blank" to="/purchaser/basket">个人中心</v-btn>
    </v-toolbar-items>
    <v-tooltip bottom>
      <v-btn slot="activator" icon large target="_blank" to="/">
        <v-icon large>store</v-icon>
      </v-btn>
      <span>返回购物大厅</span>
    </v-tooltip>
  </v-toolbar>
    <v-content>
      <v-layout row wrap >
        <v-flex xs8 class="pt-5 pl-5 pr-4">
          <v-carousel>
            <v-carousel-item v-for="(item,i) in 4" :src="`https://unsplash.it/150/300?image=${Math.floor(Math.random() * 75) + 1}`"  :key="i"></v-carousel-item>
          </v-carousel>
        </v-flex>
        <v-flex xs4 class="pt-5 pr-5 pl-2">
          <v-card>
            <v-layout row class="pl-5 pt-5">
              <v-flex xs12 ><div class="title">{{goodsInfo.name}}</div></v-flex>
            </v-layout>
            <v-layout row class="pl-5 pt-3 ">
              <v-flex xs12><div class="caption">描述：{{goodsInfo.description}}</div></v-flex>
            </v-layout>
            <v-layout row class="pl-5 pt-3 ">
              <v-flex xs12><div class="body-2">店铺：{{goodsInfo.shop}}</div></v-flex>
            </v-layout>
            <v-layout row class="pl-5 pt-3">
              <v-flex xs12><div class="body-2">地址：{{goodsInfo.place}}</div></v-flex>
            </v-layout>
            <v-layout row class="pl-5 pt-3">
              <v-flex xs12><div class="body-2"><font-awesome-icon icon="yen-sign" />&nbsp;&nbsp;{{goodsInfo.price}}</div></v-flex>
            </v-layout>
            <v-layout row class="pl-5 pt-3 pb-5">
              <v-flex xs12><div class="body-2"><font-awesome-icon icon="warehouse" />&nbsp;&nbsp;{{goodsInfo.stock}}</div></v-flex>
            </v-layout>
          </v-card>
          <v-card-actions>
            <v-btn color="orange darken-3" dark class="mb-5" :disabled="operateDisabled" @click="purchaseDia=true"><v-icon left>done</v-icon>立即购买</v-btn>
            <v-btn color="orange" dark class="mb-5" :disabled="operateDisabled" @click="basketDia=true"><v-icon left>shopping_cart</v-icon>加购物车</v-btn>
            <v-btn color="orange lighten-2" dark class="mb-5" :disabled="operateDisabled" @click="collect"><v-icon left>star_border</v-icon>收藏商品</v-btn>
          </v-card-actions>
        </v-flex>
      </v-layout>
    </v-content>
    <v-dialog v-model="purchaseDia" max-width="500px">
      <v-card>
        <v-toolbar card dark color="orange">
         <v-toolbar-title>商品购买</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon dark @click.native="purchaseDia = false">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-alert :value="alert" type="error" transition="scale-transition">{{alertInfo}}</v-alert>
        <v-card-text>
          <v-text-field v-model="goodsPurchased.num" type="number" min="1" color="orange" label="数量"></v-text-field>
          <div class="title pb-3">总额合计：&nbsp;&nbsp;{{total}}</div>
          <v-divider ></v-divider>
          <em class="grey--text pt-3">账户余额：&nbsp;&nbsp;{{purchaserInfo.balance}}<br/>
            收获地址：&nbsp;&nbsp;{{purchaserInfo.address}}<br/>
            联系方式：&nbsp;&nbsp;{{purchaserInfo.phone}}
          </em>
        </v-card-text>
        <v-card-actions>
          <v-btn color="orange" flat @click.stop="buy">下单</v-btn>
          <v-btn color="orange lighten-3" flat @click.stop="purchaseDia=false">取消</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="basketDia" max-width="500px">
      <v-card>
        <v-toolbar card dark color="orange">
          <v-icon>shopping_cart</v-icon>
          <v-toolbar-title>购物车添加</v-toolbar-title>
          <v-spacer></v-spacer>
            <v-btn icon dark @click.native="basketDia = false">
              <v-icon>close</v-icon>
            </v-btn>
        </v-toolbar>
        <v-alert :value="alert" type="error" transition="scale-transition">{{alertInfo}}</v-alert>
        <v-card-text>
          <v-text-field v-model="goodsBasket.num" type="number" min="1" color="orange" label="数量"></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-btn color="orange" flat @click.stop="basket">确认</v-btn>
          <v-btn color="orange lighten-3" flat @click.stop="basketDia=false">取消</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </v-app>
</template>

<script>
  import bus from '@/eventBus'
export default {
  name: 'GoodsInfo',
  data() {
    return {
      isLoggedIn: false,
      operateDisabled: true,
      goodsInfo: {},
      purchaserInfo: {},
      goodsPurchased:{
        num: 1,
      },//装到list里传给后端
      goodsBasket:{
        num: 1,
      },
      goodsCollected:{},
      alertInfo: '',
      purchaseDia: false,
      basketDia: false,
      alert: false,
    }
  },
  computed: {
    total(){
      return this.goodsPurchased.num * this.goodsInfo.price
    }
  },
  methods: {
    getGoodsInfo(){
      this.$http.get('/goodsInfo/'+this.$route.params.id).then((response)=>{
          this.goodsInfo=response.data;
        }
      );
    },
    getPurchaserInfo(){
      this.$http.get('/purchaserInfo/'+sessionStorage.getItem('id')).then((response)=>{
          this.purchaserInfo=response.data;
        }
      );
    },
    buy() {
      if(this.goodsPurchased.num>this.goodsInfo.stock){
        this.alertInfo='商品库存不足';
        this.alert=true;
      }
      else if(this.total>this.purchaserInfo.balance){
        this.alertInfo='账户余额不足';
        this.alert=true;
      }
      else{
        this.goodsPurchased.purchaserId=this.purchaserInfo.id;
        this.goodsPurchased.goodsId=this.goodsInfo.id;
        this.goodsPurchased.price=this.goodsInfo.price;
        var data=[];
        data.push(this.goodsPurchased);
        this.$http.post('/buy',data).then((response)=>{
          let res=response.data;
          this.alert=false;
          this.purchaseDia=false;
          bus.$emit('show-info', res.result);
        })
          .catch(function (error) {
            console.log(error);
            bus.$emit('show-info', '下单失败，请重新尝试！');
          });
      }
    },
    basket() {
      if(this.goodsBasket.num>this.goodsInfo.stock){
        this.alertInfo='商品库存不足';
        this.alert=true;
      }else{
        this.goodsBasket.purchaserId=this.purchaserInfo.id;
        this.goodsBasket.goodsId=this.goodsInfo.id;
        this.$http.post('/basket',this.goodsBasket).then((response)=>{
          let res=response.data;
          this.alert=false;
          this.basketDia=false;
          bus.$emit('show-info', res.result);
        })
          .catch(function (error) {
            console.log(error);
          });
      }
    },
    collect() {
      this.goodsCollected.purchaserId=this.purchaserInfo.id;
      this.goodsCollected.goodsId=this.goodsInfo.id;
      this.$http.post('/collect',this.goodsCollected).then((response)=>{
        let res=response.data;
        bus.$emit('show-info', res.result);
      })
        .catch(function (error) {
          console.log(error);
        });
    },

  },
  created() {
    this.getGoodsInfo();
    this.getPurchaserInfo();
    if(sessionStorage.getItem('logged')==='true'){
      this.isLoggedIn=true;
      this.operateDisabled=false
    }
  }
}
</script>

<style scoped>

</style>



