<template>
<v-container v-if="goodsList.length>0">
  <v-layout class="align-center">
    <v-flex xs12 class="pt-5 pr-4">
      <v-container grid-list-xl fluid>
        <v-layout row wrap mx-2>
          <v-flex v-for="(goods ,i) in goodsList" :key="i" xs3>
            <v-card hover>
              <v-card-media :src="`https://unsplash.it/150/300?image=${Math.floor(Math.random() * 75) + 1}`" height="130px">
                <v-container fill-height class="picture">
                  <v-layout align-center>
                    <v-flex >
                      <span class="title white--text">{{ goods.name }}</span>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card-media>
              <v-card-text class="grey--text pl-3 pb-0">
                <div class="pr-2 pl-2 pb-4">
                  <v-layout align-center>
                  <font-awesome-icon icon="yen-sign" />
                  {{goods.price}}
                  <v-spacer></v-spacer>
                  {{goods.place}}
                </v-layout>
                </div>
                <div >
                  <v-layout class="align-center justify-center">
                    <v-btn icon :to="'/goods/'+goods.id"><v-icon small color="orange">visibility</v-icon></v-btn>
                    <v-spacer></v-spacer>
                    <v-btn icon @click="deleteCollect(goods.id)"><v-icon small color="orange">delete</v-icon></v-btn>
                  </v-layout>
                </div>
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-flex>
  </v-layout>
</v-container>
  <v-container v-else>
    <v-layout class="align-center justify-center">
      <div class="display-2 grey--text pt-4 pl-3"><v-icon large>mood_bad</v-icon>还未有收藏商品哦，快去添加吧~</div>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: 'Collection',
  data() {
    return {
      goodsList:[],
    }
  },
  methods: {
    getGoodsList() {
      this.$http.get('/collect?id='+sessionStorage.getItem('id') )
        .then((response) => {
          this.goodsList = response.data
        })
    },
    deleteCollect(id){
      var data={};
      data.purId=sessionStorage.getItem('id');
      data.goodsId=id;
      this.$http.post('/deleteCollect',data)
        .then((response) => {
          this.getGoodsList()
        })
    }
  },
  created() {
    this.getGoodsList();
  }
}
</script>

<style scoped>
  .picture{
    background-color: dimgray;
    opacity: 0.6;
  }
</style>



