<template>
  <v-container v-if="goodsList.length>0">
    <!--<v-flex xs12><p>{{ selected }}</p></v-flex>-->
    <v-layout row>
    <v-flex xs8>
      <v-card flat>
        <v-container fluid style="min-height: 0;" grid-list-lg>
          <v-layout row wrap>
            <v-flex xs12 v-for="(goods ,i) in goodsList" :key="i">
              <v-card color="grey lighten-3" class="orange--text text--darken-3" hover>
                <v-container fluid grid-list-lg>
                  <v-layout row>
                    <v-flex xs3 class="pl-0">
                      <v-card-media src="/static/publisher.jpg" height="135px" contain></v-card-media>
                    </v-flex>
                    <v-flex xs6>
                      <div>
                        <div class="headline">{{goods.name}}</div>
                        <div>
                          <font-awesome-icon icon="yen-sign" />{{goods.price}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                          <font-awesome-icon icon="warehouse" />&nbsp;{{goods.stock}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                          <v-icon color="orange">place</v-icon>{{goods.place}}
                        </div>
                        <v-divider></v-divider>
                        <div class="pt-1"><v-text-field v-model="goods.num" label="数量" type="number" min="1" :max=goods.stock color="orange"></v-text-field></div>
                      </div>
                    </v-flex>
                    <v-layout class="align-center justify-center" column>
                      <v-flex xs2>
                      <v-checkbox v-model="selected" :value=goods color="orange"></v-checkbox>
                      <v-btn color="orange lighten-2" flat dark :to="'/goods/'+goods.id">商品详情</v-btn>
                    </v-flex>
                    </v-layout>
                    <v-flex xs1>
                      <div class="pt-0 pr-0"><v-btn icon @click="deleteGoods(goods.goodsId)" color="grey lighten-3"><v-icon>close</v-icon></v-btn></div>
                    </v-flex>
                   </v-layout>
                </v-container>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-flex>
    <v-flex xs4>
    <v-card>
      <v-card-title class="title">已选商品</v-card-title>
      <v-card-text>
        <div v-for="(item,index) in selected" :key="index">
          <div class="pt-2">
            {{index+1}}、&nbsp;&nbsp;名称：{{item.name}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;单价：{{item.price}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;数量：{{item.num}}<br/>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;小计：{{itemSum(item.price,item.num)}}<br/>
          </div>
          <v-divider></v-divider>
        </div>
        <div class="body-2 pt-1">
          合计：{{totalSum}}
        </div>
        <div class="pt-3">
          <em class="grey--text">账户余额：&nbsp;&nbsp;{{purchaserInfo.balance}}<br/>
            收获地址：&nbsp;&nbsp;{{purchaserInfo.address}}<br/>
            联系方式：&nbsp;&nbsp;{{purchaserInfo.phone}}
          </em>
        </div>
      </v-card-text>
      <v-card-actions v-if="selected.length>0">
        <v-btn color="orange" dark @click="buy">下单</v-btn>
      </v-card-actions>
    </v-card>
  </v-flex>
    </v-layout>
  </v-container>
  <v-container v-else>
    <v-layout class="align-center justify-center">
      <div class="display-2 grey--text pt-4 pl-3"><v-icon large>mood_bad</v-icon>还未有商品哦，快去添加吧~</div>
    </v-layout>
  </v-container>
</template>

<script>
  import bus from '@/eventBus'
export default {
  name: 'Basket',
  data() {
    return {
      purchaserInfo:{},
      goodsList:[],
      selected:[],
    }
  },
  computed: {
    totalSum() {
      var sum=0;
      for(var i=0; i<this.selected.length; i++){
        sum=sum+this.selected[i].price*this.selected[i].num
      }
      return sum;
    }
  },
  watch:{
  },
  methods:{
    getGoodsList() {
      this.$http.get('/basket?id='+sessionStorage.getItem('id') )
        .then((response) => {
          this.goodsList = response.data
        })
    },
    getPurchaserInfo(){
      this.$http.get('/purchaserInfo/'+sessionStorage.getItem('id')).then((response)=>{
          this.purchaserInfo=response.data;
        }
      );
    },
    deleteGoods(id) {
      var data={};
      data.purId=sessionStorage.getItem('id');
      data.goodsId=id;
      this.$http.post('/deleteBasket',data)
        .then((response) => {
          this.getGoodsList()
        })
    },
    itemSum(price,num) {
      return price*num;
    },
    isOverStock() {
      var res=[];
      for(var i=0; i<this.selected.length; i++){
        if(this.selected[i].num>this.selected[i].stock){
          res.push(i+1);
        }
      }
      return res;
    },
    buy() {
      for(var i=0; i<this.selected.length; i++){
        this.selected[i].purchaserId=sessionStorage.getItem('id')
        }
        if(this.isOverStock().length>0){
        bus.$emit('show-info', '第'+this.isOverStock()+'项库存不足，请核对');
        }
      else if(this.totalSum>this.purchaserInfo.balance){
        bus.$emit('show-info', '账户余额不足，请充值');
      }
      else{
        this.$http.post('/buy',this.selected).then((response)=>{
          let res=response.data;
          this.getGoodsList();
          this.getPurchaserInfo();
          this.selected=[];
          bus.$emit('show-info', res.result);
        })
          .catch(function (error) {
            console.log(error);
            bus.$emit('show-info', '下单失败，请重新尝试！');
          });
      }
    },
  },
  created() {
    this.getGoodsList();
    this.getPurchaserInfo()
  }
}
</script>

<style scoped>
</style>



