                      <v-text-field v-model="requester.occupation" prepend-icon="turned_in" label="职务" color="purple"></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs5>
                      <v-text-field v-model="requester.company" prepend-icon="card_travel" label="公司" color="purple"></v-text-field>
