                      <v-text-field v-model="purchaser.name"  :rules="[(v) => v.length <= 15 || 'Max 15 characters',(v)=>v.length>0 || 'User name is needed']" :counter="15"  label="用户名" prepend-icon="account_box" required color="orange"></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs5>
