<script>
  import bus from '@/eventBus'
  export default {
    name: "Register",
    data() {
      return {
        title: 'Lavender平台——用户注册',
        sub_title: 'Lavender Shopping Mall',
        gradient: 'to top right, rgba(0,0,90,.6), rgba(128,0,96, .6)',
        step: 1,
        userType: '',
        dateMenu: false,
        purchaser : {
          name:'',
          key:'',
          birth: null,
          sex: '0',
          phone: '',
          email: '',
          address:''
        },
        croppa: {},
      }
    },
    watch: {
      dateMenu (val) {
        val && this.$nextTick(() => (this.$refs.picker.activePicker = 'YEAR'))
      }
    },
    methods: {
      chooseType(type) {
        if(type==='shop')bus.$emit('show-info', "该功能未开发，请注册用户！");
        else {
          this.userType = type;
          this.step = 2;
        }
      },
      save(date) {
        this.$refs.menu.save(date)
      },
      purchaser_submit() {
        if (!this.croppa.hasImage()) {
          bus.$emit('show-info', "请选择头像");
          return;
        }
        else if(this.purchaser.name===''){
          bus.$emit('show-info', "用户名不可为空！");
          return;
        }
        else if(this.purchaser.key ===''){
          bus.$emit('show-info', "密码不可为空！");
          return;
        }
        else if(this.purchaser.birth === null){
          bus.$emit('show-info', "生日不可为空！");
          return;
        }
        else if(this.purchaser.email ===''){
          bus.$emit('show-info', "邮箱不可为空！");
          return;
        }
        else if(this.purchaser.address ===''){
          bus.$emit('show-info', "地址不可为空！");
          return;
        }
        else {
          this.$http.post('/registerPurchaser', this.purchaser)
            .then((response) => {
              if(response.data.result==='success'){
                this.step = 3;
              }
              else bus.$emit('show-info', response.data.result);
              //this.upload(this.worker.name, 'worker');
            })
            .catch((error) => {
              bus.$emit('show-info', "注册失败：" + error.response.data.message);
            })
        }
      },
      /*upload(name, type) {
        this.croppa.generateBlob((blob) => {
          let fd = new FormData();
          fd.append('file', blob);
          fd.append('name', name);
          fd.append('type', type);
          this.$http.post('/avatar', fd)
            .then((response) => {

            })
        })
      }*/
    }
  }
</script>

