<template>
  <v-app>
    <v-toolbar v-if="isLoggedIn === false" color="orange" dark fixed app clipped-right id="toolbar1">
      <v-toolbar-title>Lavender购物平台</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items >
        <v-btn slot="activator" color="transparent" depressed target="_blank" to="/login/purchaser">请登录</v-btn>
        <v-btn slot="activator" color="transparent" depressed target="_blank" to="/register">注册</v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-toolbar v-if="isLoggedIn === true" color="orange" dark fixed app clipped-right id="toolbar2">
      <v-toolbar-title>Lavender购物平台</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn slot="activator" color="transparent" depressed target="_blank" to="/purchaser/basket">个人中心</v-btn>
      </v-toolbar-items>
      <v-tooltip bottom>
        <v-btn slot="activator" icon large target="_blank" @click="logout">
          <v-icon large>exit_to_app</v-icon>
        </v-btn>
        <span>退出登录</span>
      </v-tooltip>
    </v-toolbar>
    <v-content>
        <v-layout row wrap >
          <v-flex xs3 class="pt-5 pl-4 pr-2">
            <v-expansion-panel popout>
              <v-expansion-panel-content v-for="(item,i) in categories" :key="i" :value="i === 0">
                <div slot="header" class="orange--text body-2" >{{item.father}}</div>
                <v-card class="pb-2 pl-2 pr-2">
                  <div class="text-xs-center">
                    <v-chip v-for="(child,i) in item.children" :key="i" outline color="orange" @click="getGoods(child)">{{child}}</v-chip>
                  </div>
                </v-card>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-flex>
          <v-flex xs9 class="pt-5 pr-4">
            <v-container v-if="goodsList.length>0" grid-list-lg fluid>
                <v-layout row wrap mx-2>
                  <v-flex v-for="(goods ,i) in goodsList" :key="i" xs2>
                    <v-card hover :to="'/goods/'+goods.id">
                      <v-card-media :src="`https://unsplash.it/150/300?image=${Math.floor(Math.random() * 75) + 1}`" height="130px">
                        <v-container fill-height class="picture">
                          <v-layout align-center>
                            <v-flex >
                              <span class="title white--text">{{ goods.name }}</span>
                            </v-flex>
                          </v-layout>
                        </v-container>
                      </v-card-media>
                      <v-card-text class="grey--text pl-3">
                        <v-layout align-center>
                          <font-awesome-icon icon="yen-sign" />
                           {{goods.price}}
                          <v-spacer></v-spacer>
                          {{goods.place}}
                        </v-layout>
                      </v-card-text>
                    </v-card>
                  </v-flex>
                </v-layout>
              </v-container>
            <v-container v-else>
              <v-layout class="align-center justify-center">
                <div class="display-2 grey--text pt-4 pl-3"><v-icon large>mood_bad</v-icon>抱歉，该品类下暂时没有商品哦~</div>
              </v-layout>
            </v-container>
          </v-flex>
        </v-layout>
    </v-content>
  </v-app>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      isLoggedIn: false,
      categories:[
        {father: '情感类', children: ['女性','青春','人物','励志','鸡汤','抒情散文','爱情']},
        {father: '科幻类', children: ['冒险','奇幻','机器人','末世想象']},
        {father: '艺术类', children: ['建筑','音乐','哲学', '美术', '语言']},
      ],
      goodsList: []
    }
  },
  methods: {
    getGoods (category) {
      this.$http.get('/goods?type=' + category)
        .then((response) => {
          this.goodsList = response.data
        })
    },
    logout(){
      sessionStorage.setItem('logged', 'false');
      this.$router.push("/login/purchaser");
    }

  },
  created () {
    this.getGoods('all')
    if(sessionStorage.getItem('logged')==='true') this.isLoggedIn=true
  }

}
</script>

<style scoped>
  .picture{
    background-color: dimgray;
    opacity: 0.6;
  }
</style>



